var staticRenderFns = [function (){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"container"},[_c('section',{staticClass:"content"},[_c('h1',{staticClass:"lh-lg"},[_vm._v("江西省上饶市鄱阳县双港乡")]),_c('img',{attrs:{"src":require("@/assets/img/township/tlh/home.png"),"alt":"江西省上饶市鄱阳县双港乡"}}),_c('br'),_c('br'),_c('h2',{staticClass:"lh-lg"},[_vm._v("一、简介")]),_c('p',{staticClass:"text-start lh-lg"},[_vm._v("莲湖乡是全国第一大淡水湖——鄱阳湖中一岛乡。")]),_c('p',{staticClass:"text-start lh-lg"},[_vm._v(" 位于鄱阳县城西南隅，鄱阳湖东南岸边。总面积为251平方公里。有耕地41427亩，其中旱地15381亩，水田26046亩（含南北两座圩围垦田），养殖水面2万亩，湖坪草洲146384亩，全乡共34个村委会，总人口71800人。 ")]),_c('p',{staticClass:"text-start lh-lg"},[_vm._v(" 莲湖乡的自然资源特别是水产资源得天独厚，乡内大小湖库塘汊众多，水质优良，可养殖水面总计达20000亩，水产藏量极为丰富，既有鱼虾、又有龟甲，珍禽和丰厚的可食性水生植物等十百种，其中鳖鱼、银鱿尤为珍贵。 ")]),_c('p',{staticClass:"text-start lh-lg"},[_vm._v(" 乡内筑有精养鱼池有3600余亩，每块15亩左右，并配有鱼种池80块，可供孵化、繁育。丰富水产资源可供螃蟹、珍珠等物种水产品养殖与开发。 ")]),_c('h2',{staticClass:"lh-lg"},[_vm._v("二、著名景点")]),_c('h3',{staticClass:"lh-lg"},[_vm._v("桥头吴氏宗祠")]),_c('img',{attrs:{"src":require("@/assets/img/township/tlh/wszc.png"),"alt":"桥头吴氏宗祠"}}),_c('br'),_c('br'),_c('p',{staticClass:"text-start lh-lg"},[_vm._v(" 吴氏宗祠在鄱阳县谢家滩镇鸡峰桥头村，保存完好，据载是被司马迁誉为“世家第一”的吴太伯后人所建。鸡峰吴氏宗祠建于明成化年间。鸡峰桥头村，距鄱阳县城70多公里。桥头村吴氏宗祠，不仅是鄱阳吴氏族人珍贵的文化遗产，也是鄱阳人民的宝贵文化财富。 鸡峰桥头吴氏宗祠，仍属徽派建筑，粉墙皂瓦保留完整，女儿墙高达丈余，气势恢宏。除正门外墙有两个小窗外，其他方向墙面没有窗户。东西各有一个偏门，现在进出是走西边的侧门，大门紧锁，管理严格。祠堂内木雕、石雕、砖雕都非常精细，油漆鲜亮，遍布各个墙面、廊柱和栋梁之上，保存完好。人物形象生动活泼、眼目传神，花草景物，搭配合理，栩栩如生。似乎毎一块雕刻都在讲述着一个祖先的故事，使我们在凝望之时，肃然起敬，默默沉思。尤其是大厅和戏台中央上方旋涡状造型的斗拱，设计新颖，造型独特，工艺精湛，让人称奇！为此，“鸡峰桥头吴氏宗祠建筑技艺”被列为“鄱阳县非物质文化遗产保护目录”。 ")]),_c('h3',{staticClass:"lh-lg"},[_vm._v("蜈蚣山")]),_c('img',{attrs:{"src":require("@/assets/img/township/tlh/wgs.png"),"alt":"蜈蚣山"}}),_c('br'),_c('br'),_c('p',{staticClass:"text-start lh-lg"},[_vm._v(" 蜈蚣山位于江西省上饶市鄱阳县田畈街镇松林村，山上一年四季树木常青，山上到了春天长满了杜鹃，远远望去恰似一片红色的海洋，到了夏天长满了各种野果子，小朋友们成群结队的到山上游玩，一边玩一边摘野果子吃，山上还有一个有趣的山洞，人可以钻进去玩，最有趣的是山上有一块大石头，石头上有一个小井，井里的水一年四季都不会干，到了秋天山上又长满了野栗子，大人，小孩都拿着剪刀和菜篮子到山上采栗子.到了冬天山上被大雪覆盖，弯延着恰似一条正在爬行的大蜈蚣.山下是一所小学，每天早晨学生的读书声和山上的鸟鸣声不正是一幅美丽的画卷。 ")]),_c('h2',{staticClass:"lh-lg"},[_vm._v("三、特产")]),_c('h3',{staticClass:"lh-lg"},[_vm._v("庙前白莲")]),_c('img',{attrs:{"src":require("@/assets/img/township/tlh/bl.png"),"alt":"庙前白莲"}}),_c('br'),_c('br'),_c('p',{staticClass:"text-start lh-lg"},[_vm._v(" 白莲，又称家莲，主产于鄱阳三庙前。庙前白莲，栽培历史悠久，不但栽培面积多、产量高，而且以质量上乘与广昌白莲齐名。 白莲是人工栽培的水生植物，开白花，藕细实多，莲果壳薄仁大，富含淀粉、蛋白质、脂肪、糖、钙、磷、铁等多种微量元素和各种维生素。 ")]),_c('br'),_c('br'),_c('p',{staticClass:"text-center lh-lg"},[_vm._v(" 文字撰写 | 周子路 "),_c('br'),_vm._v(" 信息来源 | 百度百科、上饶市政府官网 ")])])])
